.filter{
    &-wrap{
        border: 1px solid #e1e1e1;
        margin-bottom: 60px;
        padding: 30px;
        i.close{
            font-size: 10px;
            color: #a1a1a1;
            margin-left: 5px;
            cursor: pointer;
            transition: all .3s ease-out;
            &:hover{
                color: #ff0000;
                transition: all .3s ease-out;
            }
        }

    }
    &Item{
        display: none;
        &.show{
            display: flex;
            transition: 1s;
        }
    }
    &-Btn{
        padding: 20px 0;
        text-align: center;
        align-items: center;
        justify-content: center;
        display: none;
        i{
            margin-right: 20px;
        }
    }
}
.select{
    &-text{
        position: relative;
        padding: 10px 30px 10px 15px;
        cursor: pointer;
        input{
            width: 100%;
        }
        span{
            line-height: normal;
            user-select: none;
            white-space: nowrap;
            text-overflow: ellipsis;
            overflow: hidden;
            display: block;
        }
        &::after{
            position: absolute;
            content: "\E008";
            font-family: "icons";
            font-size: 7px;
            top: calc(50% - 3.5px);
            right: 15px;
        }
        &.input{
            input{
                border: none;
                font-size: inherit;
                color: inherit;
                &:focus{
                    outline: none;
                }
                &::placeholder{
                    color: #000000;
                }
            }
        }
        
    }
    &-wrap{
        position: relative;
        width: 100%;
        margin-bottom: 20px;
        &:not(:first-of-type){
            margin-left: 4px;
        }
        &.open{
            z-index: 3!important;
            .select-text{
                border-color: #e08b44;
            }
            .dropdown{
                display: block;
            }
        }
        .dropdown{
            top: calc(100% + 10px);
            font-size: 13px;
            width: 100%;
            min-width: auto;
            overflow-y: auto;
            height: 232px;
            padding: 15px 12px 15px 15px;
            z-index: 1;
            li{
                padding: 0;
                label{
                    position: relative;
                    padding: 6px 0;
                    margin: 5px 0;
                    display: inline-block;
                    &::after{
                        position: absolute;
                        content: "";
                        width: 31px;
                        height: 31px;
                        border: 1px solid #e1e1e1;
                        background-color: #ffffff;
                        top: 0;
                        left: 0;
                        border-radius: 3px;
                        transition: all .3s ease-out;
                    }
                    &:hover{
                        &::after{
                            border-color: #445f87;
                            transition: all .3s ease-out;
                        }
                        span{
                            color: #e08b44;
                            transition: all .3s ease-out;
                        }
                    }
                }
                .checkItem{
                    margin: 0 13px 0 9px;
                }
                span{
                    transition: all .3s ease-out;
                    user-select: none;
                }
                sup{
                    font-size: 11px;
                    padding: 0 4px;
                    color: #445f87;
                }
            }
            .selected{
                label{
                    &::after{
                        border-color: #445f87;
                    }
                    &::before{
                        position: absolute;
                        content: "\E037";
                        font-family: "icons";
                        font-size: 13px;
                        color: #445f87;
                        left: 9px;
                        top: 9px;
                        z-index: 1;
                    }
                }
                span{
                    color: #e08b44;
                }
            }
        }
        &.Notarrow{
            .select-text{
                padding-right: 15px;
                &::after{
                    display: none;
                }
            }
        }
    }
}
.selected-field{
    display: flex;
    align-items: baseline;
    color: #010101;
    font-size: 13px;
    padding: 10px 40px 10px 0;
    p{
        margin: 0 10px 0 0;
    }
    span{
        color: #e08b44;
    }
    sup{
        font-size: 11px;
        color: #445f87;
        padding: 0 4px;
    }
    b,em{
        display: none;
    }
    &s{
        position: relative;
        display: flex;
        align-items: center;
        padding: 20px 0;
        margin: 10px 0 0;
        flex-wrap: wrap;
        &::after{
            position: absolute;
            content: "";
            width: calc(100% + 60px);
            height: 100%;
            top: 0;
            left: -30px;
            background-color: #f9f9f9;
            z-index: -1;
        }
    }
}
.filter__Btns{
    margin-top: 30px;
    &, .left{
        display: flex;
        align-items: center;
    }
    .left{
        margin-right: auto;
        .Btn-Wedgewood{
            margin-right: 30px;
        }
        .Btn-Nobel{
            transition: .3s;
        }
        .Btn-Nobel:hover{
            &, i{
                color: #ff0000;
                transition: .3s;
            }
        }
    }
}
/* сортировка  */
.sorting__wrap{
    display: flex;
    align-items: flex-start;
    margin: 20px 0 40px;
}
.sorting{
    display: flex;
    align-items: center;
    p{
        font-size: 13px;
        color: var(--Charcoal);
        margin: 0 8px 0 0;
    }
}
.sort-quantity{
    margin-left: auto;
    margin-right: 11px;
    .select-wrap[data-name='sorting']{
        width: 76px;
    }
}
.select-wrap[data-name='sorting']{
    margin-bottom: 0;
    width: 177px;
    z-index: 2;
    .select-text{
        padding: 7px 30px 8px 15px;
    }
    .dropdown{
        height: auto;
        label{
            padding: 4px 0;
            &::after, &::before{
                display: none;
            }
        }
        .radioItem{
            display: none;

        }
    }
}
@media(max-width: 767px){
    .sorting__wrap{
        margin: 10px 0 20px;
        .sorting:not(.sort-quantity){
            margin-right: 10px;
        }
    }
    .filter{
        &-wrap{
            padding: 0 20px;
            margin-bottom: 20px;
        }
        &-inner{
            padding: 20px 0;
            border-bottom: 1px dashed #e1e1e1;
            display: none;
        }
        &-Btn{
            display: flex;
        }
    }
    .selected-field{
        padding-right: 30px;
    }
    .selected-fields::after{
        left: -20px;
         width: calc(100% + 40px);
    }
}
@media(max-width: 700px){
    .filter__Btns{
        flex-direction: column;
        align-items: flex-start;
        margin-top: 20px;
        .left{
            margin-left: 0;
            margin-bottom: 10px;
        }
    }
    .selected-fields{
        padding: 10px 0;
        margin-top: 0;
    }
}
@media(max-width: 575px){
    .sorting__wrap{
        flex-direction: column;
    }
    .sort-quantity{
        margin: 10px 0 0;
    }
    .sorting{
        width: 100%;
        .select-wrap[data-name='sorting']{
            width: calc(100% - 97px);
            margin-left: auto;
        }
    }
    .filter-wrap{
        padding: 0 10px;
        .select-wrap{
            margin-bottom: 10px;
        }
    }
    .selected-fields{
        margin-top: 10px;
        &::after{
            left: -10px;
            width: calc(100% + 20px);
        }
    }
    .selected-field{
        flex-direction: column;
        padding-right: 25px;
        p{
            margin-bottom: 10px;
        }
    }
}